import { BC_FUNCTIONAL_ICONS } from './bc-functional-icons-list';
import { BC_ILLUSTRATION_IMAGES } from './bc-ilustration-images-list';
import { BC_LOGOS } from './bc-logo-list';
import { BC_PICTOGRAM_IMAGES_LIST } from './bc-pictogram-images-list';
import { BC_STROKE_IMAGES_LIST } from './bc-stroke-images-list';

const KIND_NAMES_ICONS = {
  pictograms: BC_PICTOGRAM_IMAGES_LIST,
  strokes: BC_STROKE_IMAGES_LIST,
  illustrations: BC_ILLUSTRATION_IMAGES,
  functionals: BC_FUNCTIONAL_ICONS,
  logos: BC_LOGOS,
};

const MAP = {};

for (const key in KIND_NAMES_ICONS) {
    if (Object.prototype.hasOwnProperty.call(KIND_NAMES_ICONS, key)) {
        const element = KIND_NAMES_ICONS[key];
        for (const icon of element) {
            MAP[icon] = key;
          }
    }
}

export default MAP;

